
import { defineComponent, ref } from 'vue'
import XeroRepository from '@/repository/XeroRepository';

interface FormState {
  name: string;
  firstName: string;
  lastName: string;
  accountNumber: string;
  contactStatus: 'ACTIVE' | 'ARCHIVED' | 'DELETED';
  companyNumber: string;
  emailAddress: string;
  skypeUserName: string;
  bankAccountDetails: string;
  taxNumber: string;
  addresses: {
    addressLine1: string;
    addressLine2: string;
    city: string;
    region: string;
    postalCode: string;
    country: string;
  }[];
  phones: {
    phoneType: string;
    phoneNumber: string;
  }[];
  isSupplier: boolean;
  isCustomer: boolean;
}


export default defineComponent({
    props: {
      showForm: Boolean,
      po: Object,
    },
    data() {
      return {
        visible: ref(false),
        formState: ref<FormState>({
        name: '',
        firstName: '',
        lastName: '',
        accountNumber: '',
        contactStatus: 'ACTIVE',
        companyNumber: '',
        emailAddress: '',
        skypeUserName: '',
        bankAccountDetails: '',
        taxNumber: '',
        addresses: [
            {
            addressLine1: '',
            addressLine2: '',
            city: '',
            region: '',
            postalCode: '',
            country: '',
            },
        ],
        phones: [
            {
            phoneType: 'Mobile Phone',
            phoneNumber: '',
            },
        ],
        isSupplier: false,
        isCustomer: false,
        }),
      };
    },
    methods: {
      onClose() {
        this.$emit("close")
      },
      onSubmit() {
        this.createContact(this.removeNullValues(this.formState));
      },
      async createContact(body) {
        console.log(this.replaceEmptyStringWithNull(body));
        await XeroRepository.createContact(this.replaceEmptyStringWithNull(body))
            .then(() => {
                this.$message.success("Successfully Create Contact");
                this.$emit('update');
            }).catch((err) => {
                this.$message.error("Failed to Create Contact");
                window.console.log(err);
            });
      },
      replaceEmptyStringWithNull(obj) {
        const result: any = {}; // we cast it to 'any' to avoid TypeScript errors when deleting properties
        for (const [key, value] of Object.entries(obj)) {
            const updatedValue = value === '' ? null : value;
            result[key] = updatedValue;
        }
        return result;
      },
      removeNullValues(obj: any) {
        const result: any = {}; // we cast it to 'any' to avoid TypeScript errors when deleting properties

        for (const [key, value] of Object.entries(obj)) {
            if (value !== null) {
            result[key] = value;
            }
        }

        return result;
      },
      addAddress() {
        this.formState.addresses.push({
          addressLine1: '',
          addressLine2: '',
          city: '',
          region: '',
          postalCode: '',
          country: ''
        })
      },
      addPhone() {
        this.formState.phones.push({
          phoneType: '',
          phoneNumber: ''
        })
      },
    },
    watch: {
      showForm() {
        this.visible = this.showForm;
      },
    },
});
