
import { defineComponent, ref } from 'vue'
import ContactForm from './ContactForm.vue';
import XeroRepository from '@/repository/XeroRepository';

export default defineComponent({
    // eslint-disable-next-line vue/multi-word-component-names
    name: "contacts",
    components: {
      ContactForm,
    },
    data() {
        return {
          contacts: [],
          selectedContact: {},
          showForm: ref(false),
          formContent: ref({}),
        };
    },
    methods: {
      async findAllContact() {
        this.showForm = false;
        const { data } = await XeroRepository.getAllContact();
        this.contacts = data.Contacts;
        if (this.contacts.length > 0) this.selectedContact = this.contacts[0];
      },
      createContact() {
        this.showForm = true;
        this.formContent = {};
      },
      select(e) {
        this.selectedContact = e;
      },
      getReceivable(balance) {
        if (!balance) return {
          "Outstanding": 0,
          "Overdue": 0,
        };
        const data = balance.AccountsReceivable;
        return {
          "Outstanding": data.Outstanding,
          "Overdue": data.Overdue,
        };
      },
      getPayable(balance) {
        if (!balance) return {
          "Outstanding": 0,
          "Overdue": 0,
        };
        const data = balance.AccountsPayable;
        return {
          "Outstanding": data.Outstanding,
          "Overdue": data.Overdue,
        };
      },
    },
    mounted() {
      this.findAllContact();
    }
});
